@import 'components/variables';

.title-page {
  h1 {
    font-size: 3.5rem;
    font-weight: 800;
    color: $violet;
    margin: 0;
  }
}
.data-search {
  text-align: end;
  .icon {
    width: 1.5rem;
    margin: 0 0.5rem;
  }
}

.consulta-actividades {
  border: 1px solid $text-gray;
  margin-bottom: 2rem;
  .title {
    padding: 1rem;
    background-color: rgba($color: $violet, $alpha: 0.2);
    span {
      font-size: 1.5rem;
      font-weight: 800;
    }
  }
  .form {
    padding: 1rem;
    border-top: 1px solid $text-gray;
    label {
      margin-bottom: 0;
      span {
        color: $violet;
      }
    }
    input, textarea {
      margin-bottom: 1rem;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid $text-gray;
      padding: 0.2rem;
      &:focus {
        box-shadow: none;
      }
    }
    textarea {
      width: 100%;
      resize: none;
      &:focus-visible {
        outline: none;
      }
    }
  }
}

.mi-reserva {
  border: 1px solid $text-gray;
  .title {
    padding: 1rem;
    background-color: rgba($color: $violet, $alpha: 0.5);
    span {
      font-size: 1.5rem;
      font-weight: 800;
    }
  }
  .content {
    padding: 1rem;
    border-top: 1px solid $text-gray;
    p {
      margin: 0;
    }
    .content-item {
      display: flex;
      justify-content: space-between;
      input.cantidad {
        text-align: center;
        border: none;
        background: transparent;
        margin: 0 0.5rem;
        color: var(--primary-color);
        font-size: 1rem;
        width: 35px;
        padding: 0 5px;
        @media (max-width: 500px) {
          margin: 0;
        }
      }
      input.cantidad:focus-visible {
        outline: none;
      }
      input.cantidad::-webkit-outer-spin-button,
      input.cantidad::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      input.cantidad {
        -moz-appearance: textfield;
      }
      .mas,
      .menos {
        border-radius: 50px;
        border: none;
        background-color: #eee;
        color: $violet;
        font-size: 0.8rem;
        padding: 0 5px;
      }
      .button {
        text-align: center;
        button {
          font-size: 1.4rem;
          font-weight: bold;
          padding: 0.5rem 1.5rem;
          margin-top: 1rem;
        }
      }
    }
  }
}

.detalle-alojamiento {
  .encabezado {
    display: flex;
    flex-wrap: wrap;
    h5 {
      flex: 100%;
      font-size: 1.4rem;
      font-weight: 800;
      @media (min-width: 768px) {
        flex: 60%;
      }
      @media (min-width: 1200px) {
        flex: 70%;
      }
    }
    .valoracion {
      text-align: end;
      flex: 100%;
      @media (min-width: 768px) {
        flex: 40%;
      }
      @media (min-width: 1200px) {
        flex: 30%;
      }
      .valor {
        position: relative;
        background-color: $violet;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        color: white;
        font-size: 1.2rem;
        font-weight: 800;
        margin-right: 1rem;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: -10px;
          bottom: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent $violet transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
      }
    }
  }
  .accordion-button:not(.collapsed) {
    color: $text-gray;
    font-weight: bold;
    background-color: #baa4c1;
    box-shadow: none;
  }
  .accordion-button {
    &:focus {
      border-color: $violet;
      box-shadow: none;
    }
  }
  .accordion-body {
    ul {
      list-style: none;
      padding-left: 0;
    }
    .enlace-politicas {
      color: $text-gray;
      font-weight: 700;
      &:hover {
        color: $violet;
      }
    }
  }
}
.location {
  a {
    color: $violet;
    &:hover {
      color: $yellow;
    }
  }
}
.slider-detalle-alojamiento {
  .slick-slide {
    padding: 0 3px;
    .content {
      width: 100%;
      height: 100%;
      overflow: hidden;
      img {
        object-fit: cover;
        object-position: center;
        transition: all 0.5s;
        @media (orientation: landscape) {
          height: 50vh;
        }
        @media (orientation: portrait) {
          height: 50vw;
        }
      }
    }
  }
  .slick-slide.slick-center {
    .content {
      img {
        &:hover {
          transform: scale(1.2);
        }
      }
    }
  }
  .slick-prev, .slick-next {
    color: white;
    font-size: 1rem;
    width: 2rem;
    height: 2rem;
    background-color: rgba($color: $text-gray, $alpha: 0.5);
    z-index: 1;
    &:before {
      display: none;
    }
  }
  .slick-prev {
    left: 1rem;
  }
  .slick-next {
    right: 1rem;
  }
}
#tabDetalle {
  border-bottom: 1px solid $text-gray;
  .nav-item {
    .nav-link {
      font-size: 1.2rem;
      font-weight: 800;
      color: $text-gray;
      transition: all 0.3s;
      &.active,
      &:hover {
        color: $violet;
      }
      &.active {
        border: 1px solid transparent;
        background-color: transparent;
      }
    }
  }
}
#tabContentDetalle {
  .tab-pane {
    padding: 2rem 1rem;
    .title-pane {
      display: flex;
      align-items: center;
      margin-bottom: 1rem;
      img {
        width: 3rem;
        margin-right: 1rem;
      }
      h3 {
        font-weight: 800;
      }
    }
    ul {
      padding-left: 1rem;
    }
  }
}
hr {
  background-color: $text-gray;
  opacity: 1;
}
#modalPoliticas {
  z-index: 3050;
  .modal-body {
    .list-politicas {
      list-style: none;
      padding-left: 0;
      .item-list {
        padding: .5rem 0;
      }
    }
  }
}